<template>
  <a-card :bordered="false">
    <!---->
    <a-form class="searchForm">
      <a-row :gutter="24">
        <a-col :span="8" :lg="6">
          <a-form-item
            label=""
            :labelCol="searchFormLayout.labelCol"
            :wrapperCol="searchFormLayout.wrapperCol">
            <a-input  placeholder=""/>
          </a-form-item>
        </a-col>
        <!---->
        <a-col :span="8" :lg="6">
          <a-form-item
            label=""
            :labelCol="searchFormLayout.labelCol"
            :wrapperCol="searchFormLayout.wrapperCol">
            <a-select showSearch :style="{width: '100%'}" placeholder="">
              <a-select-option value="jack">Jack</a-select-option>
              <a-select-option value="lucy">Lucy</a-select-option>
              <a-select-option value="tom">Tom</a-select-option>
            </a-select>
          </a-form-item>
        </a-col>
        <!---->
        <a-col :span="8" :lg="6">
          <a-form-item
            label=""
            :labelCol="searchFormLayout.labelCol"
            :wrapperCol="searchFormLayout.wrapperCol">
            <a-date-picker :style="{width: '100%'}"/>
          </a-form-item>
        </a-col>
        <a-col :span="24" :lg="6" class="action">
          <a-button type="primary"></a-button>
          <a-button></a-button>
        </a-col>
      </a-row>
    </a-form>
    <!---->
    <div class="table-operations">
      <a-button type="primary" icon="plus"></a-button>
      <a-button type="primary" icon="file-excel"></a-button>
      <a-button type="primary" icon="printer"></a-button>
    </div>
    <!---->
    <a-alert
      class="table-operations"
      :message="` ${selectedUsers.length} ,  ${getSelectedCalls} `"
      description=""
      showIcon></a-alert>

    <a-spin :spinning="loading">
      <!---->
      <a-table :dataSource="users" :rowSelection="rowSelection" :columns="columns" rowKey="id">
        <!--slot-->
        <div slot="user" slot-scope="text">
          <router-link :to="`/admin/users/${text.id}`"></router-link>
          <a-divider type="vertical"/>
          <a href="" @click.prevent="deleteUser(text)"></a>
        </div>
      </a-table>
    </a-spin>
  </a-card>
</template>

<script>
import { Vue, Component } from 'vue-property-decorator';
import { observer } from 'mobx-vue';
import store from './UserStore';


@observer
@Component
export default class Users extends Vue {
  searchFormLayout = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
  };
  columns = [{
    title: '',
    dataIndex: 'name',
    key: 'name',
    scopedSlots: {
      filterDropdown: 'filterDropdown',
      filterIcon: 'filterIcon',
      customRender: 'customRender',
    },
  }, {
    title: '',
    dataIndex: 'age',
    key: 'age',
    sorter: (a, b) => a.age - b.age,
  }, {
    title: '',
    dataIndex: 'address',
    key: 'address',
    width: '25%',
    filters: [{
      text: '',
      value: '',
    }, {
      text: '',
      value: '',
    }],
    onFilter: (value, record) => record.address.indexOf(value) === 0,
  }, {
    title: '',
    dataIndex: 'createTime',
    key: 'createTime',
  }, {
    align: 'right',
    title: '()',
    dataIndex: 'call',
    key: 'call',
    sorter: (a, b) => a.call - b.call,
  }, {
    align: 'center',
    title: '',
    key: 'x',
    scopedSlots: { customRender: 'user' },
  }];

  users = [];
  loading = false;
  confirmLoading = false;
  selectedUsers = [];

  /**
   * total
   * @returns {*|number}
   */
  get getSelectedCalls() {
    return this.selectedUsers.reduce((pre, cur) => pre + cur.call, 0);
  }

  /**
   * props
   * @returns {{onChange: onChange}}
   */
  get rowSelection() {
    return {
      onChange: (selectedRowKeys, selectedRows) => {
        this.selectedUsers = selectedRows;
      },
    };
  }

  deleteUser(uesr) {
    this.$confirm({
      title: ` ${uesr.name}`,
      okType: 'danger',
      content: '',
      onOk() {},
      onCancel() {},
    });
  }

  load(status) {
    this.loading = status;
  }

  async mounted() {
    const { data } = await store.getUsers();
    this.users = data.list;
  }
}

</script>

<style lang='less' scoped>
  .searchForm {
    .action {
      text-align: right;
    }
  }

  @media (min-width: 992px) {
    .searchForm {
      .action {
        padding-top: 5px;
        text-align: left;
      }
    }
  }
</style>



